import {mapState} from 'vuex'

export default {
  name: "OReward",
  props: ['vReward', 'rate'],
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo,
      isLogin: (state) => state.isLogin
    }),
    stUser() {
      return this.vReward.stUser || {}
    },
    canAnswer() {

    }
  },
  methods: {
    goAnswer(vReward) {
      // console.log(key);
      this.$router.push({
        path: '/answer',
        query: {
          iRewardId: vReward.iRewardId
        }
      })
    },
  },
}
